import { getChannels } from '../api/channel'

export function addArea(area){
    return {
        type: "ADD_AREA",
        payload: area
    }
}

function addAreaAsync(area) {
    return dispatch => {
        dispatch(addArea(area));
        setTimeout(() => {
            dispatch(addArea());
        }, 1000);
    };
}



